.paper {
  position: relative;
  width: 90%;
  max-width: 800px;
  min-width: 400px;
  height: 200px;
  margin: 0 auto;
  background: #fafafa;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
