/* custom radio bttn */

.customRadioBttn {
  display: block;
  position: relative;
  padding-left: 35px;
  /* margin-bottom: 12px; */
  cursor: pointer;
  /* font-size: 22px; */
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font-family: Lato;
  font-size: 18px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1;
  letter-spacing: normal;
  color: #545252;
}

.customRadioBttn:hover {
  font-family: Lato;
  font-size: 18px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1;
  letter-spacing: normal;
  color: #7d6fa9;
}

/* Hide the browser's default radio button */
.customRadioBttn input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

/* Create a custom radio button */
.checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background-color: #eee;
  border-radius: 50%;
}

/* On mouse-over, add a grey background color */
.customRadioBttn:hover input~.checkmark {
  background-color: #c8bfe0;
  /* background-color: #7f70a5; */
}

/* When the radio button is checked, add a blue background */
.customRadioBttn input:checked~.checkmark {
  background-color: #7f70a5;
}

/* Create the indicator (the dot/circle - hidden when not checked) */
.checkmark:after {
  content: "";
  position: absolute;
  display: none;
}

/* Show the indicator (dot/circle) when checked */
.customRadioBttn input:checked~.checkmark:after {
  display: block;
}

/* Style the indicator (dot/circle) */
.customRadioBttn .checkmark:after {
  top: 9px;
  left: 9px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: white;
}

.customRadioSectionInfoLeft {
  margin-right: 10%;
}

.customRadioSectionInfoRight {
  margin-left: 10%;
}

.customRadioSectionInfo p {
  margin-top: 0;
  margin-bottom: 0;
}
